<template>
  <div class="content">
    <!-- 面包屑  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>IPFS节点信息</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a>节点信息</a>
      </el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索框 -->
    <div class="seach_title">
      <div class="seach_title_input">
        <el-input
          placeholder="节点ID，节点IP"
          v-model="seachinput"
          @keyup.enter.native="seachipfs()"
        >
          <i
            slot="suffix"
            class="el-input__icon el-icon-search"
            @click="seachipfs"
          ></i>
        </el-input>
      </div>
      <div class="seach_top_right" @click="option_display()">
        筛选
        <i
          class="el-icon-caret-bottom"
          :class="[rotate ? 'fa fa-arrow-down go' : 'fa fa-arrow-down aa']"
        ></i>
      </div>
    </div>
    <!--  -->
    <div class="seach_bottom" v-if="optiondisplay">
      <div class="seach_bottom_tw">
        <div class="location_select">
          <span>节点状态：</span>
          <el-select
            placeholder="请选择状态"
            v-model="value"
            @change="handleChange"
          >
            <el-option
              v-for="(item, index) in stateopt"
              :key="item.value + index"
              :label="item.label"
              :value="item.value"
            ></el-option
          ></el-select>
          <span>节点城市：</span>
          <el-cascader
            placeholder="请选择区域"
            v-model="value1"
            :options="citylist"
            @change="handleChange"
          ></el-cascader>
        </div>
        <div class="region_select">
          <span>运营商：</span>
          <el-select v-model="value2" placeholder="请选择" @change="seachipfs">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </div>
      </div>
      <div style="margin-top: 5px;">
        <el-button type="primary" plain size="mini" @click="seach"
          >确定</el-button
        >
        <el-button
          plain
          size="mini"
          @click="resetseach()"
          class="resetseach_btn"
          >重置</el-button
        >
      </div>
    </div>
    <!-- 主体表格 -->
    <div style="text-align:right;padding: 10px;" v-if="tableData.lenth > 0">
      <el-button type="primary" @click="exportExcel()">导出</el-button>
    </div>
    <el-table
      :data="tableData"
      border
      :cell-style="rowClass"
      :header-cell-style="headClass"
      style="width: 100%;"
    >
      <el-table-column prop="nodeId" label="节点ID"></el-table-column>
      <el-table-column prop="state" label="节点状态">
        <template slot-scope="scope">
          <div v-if="scope.row.state == 0">启用</div>
          <div v-else style="color:red;">禁用</div>
        </template>
      </el-table-column>
      <el-table-column prop="ip" label="节点IP"></el-table-column>
      <el-table-column prop="city" label="节点城市"></el-table-column>
      <el-table-column prop="area" label="节点区域"></el-table-column>
      <el-table-column prop="isp" label="节点运营商"></el-table-column>
      <el-table-column prop="totalBW" label="总带宽"></el-table-column>
      <el-table-column prop="occupyBW" label="占用带宽"></el-table-column>
      <el-table-column prop="remainingBW" label="剩余带宽"></el-table-column>
      <el-table-column prop="totalCap" label="总空间"></el-table-column>
      <el-table-column prop="occupyCap" label="占用空间"></el-table-column>
      <el-table-column prop="remainingCap" label="剩余空间"></el-table-column>
      <el-table-column prop="useRate" label="节点平均利用率"></el-table-column>
      <el-table-column prop label="操作">
        <template slot-scope="scope">
          <div>
            <el-button @click="handleClick(scope.row)" type="text" size="small"
              >详情</el-button
            >
            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small">详情</el-button> -->
          </div>
        </template>
      </el-table-column>
    </el-table>
    <fenye
      style="text-align: right;margin:20px 0 0 0;"
      @fatherMethod="getpage"
      @fathernum="gettol"
      :pagesa="total_cnt"
      :currentPage="currentPage"
      v-if="tableData.lenth > 0"
    ></fenye>
  </div>
</template>

<script>
import fenye from "@/components/cloudfenye";
import { query_node } from "../../servers/api";
import axios from "axios";
export default {
  data() {
    return {
      currentPage: 1,
      seachinput: "",
      value: -1,
      stateopt: [
        {
          value: -1,
          label: "全部"
        },
        {
          value: 0,
          label: "离线"
        },
        {
          value: 1,
          label: "在线"
        }
      ],
      citylist: [
        {
          value: 0,
          label: "全部"
        },
        {
          value: "华北",
          label: "华北",
          children: [
            {
              value: "北京",
              label: "北京"
            },
            {
              value: "内蒙古",
              label: "内蒙古"
            },
            {
              value: "山西",
              label: "山西"
            },
            {
              value: "河北",
              label: "河北"
            },
            {
              value: "天津",
              label: "天津"
            }
          ]
        },
        {
          value: "西北",
          label: "西北",
          children: [
            {
              value: "宁夏",
              label: "宁夏"
            },
            {
              value: "陕西",
              label: "陕西"
            },
            {
              value: "甘肃",
              label: "甘肃"
            },
            {
              value: "qinghai",
              label: "青海"
            },
            {
              value: "新疆",
              label: "新疆"
            }
          ]
        },
        {
          value: "东北",
          label: "东北",
          children: [
            {
              value: "黑龙江",
              label: "黑龙江"
            },
            {
              value: "吉林",
              label: "吉林"
            },
            {
              value: "辽宁",
              label: "辽宁"
            }
          ]
        },
        {
          value: "华东",
          label: "华东",
          children: [
            {
              value: "福建",
              label: "福建"
            },
            {
              value: "江苏",
              label: "江苏"
            },
            {
              value: "安徽",
              label: "安徽"
            },
            {
              value: "山东",
              label: "山东"
            },
            {
              value: "上海",
              label: "上海"
            },
            {
              value: "浙江",
              label: "浙江"
            }
          ]
        },
        {
          value: "华中",
          label: "华中",
          children: [
            {
              value: "河南",
              label: "河南"
            },
            {
              value: "湖北",
              label: "湖北"
            },
            {
              value: "江西",
              label: "江西"
            },
            {
              value: "湖南",
              label: "湖南"
            }
          ]
        },
        {
          value: "西南",
          label: "西南",
          children: [
            {
              value: "贵州",
              label: "贵州"
            },
            {
              value: "云南",
              label: "云南"
            },
            {
              value: "重庆",
              label: "重庆"
            },
            {
              value: "四川",
              label: "四川"
            },
            {
              value: "西藏",
              label: "西藏"
            }
          ]
        },
        {
          value: "华南",
          label: "华南",
          children: [
            {
              value: "广东",
              label: "广东"
            },
            {
              value: "广西",
              label: "广西"
            },
            {
              value: "海南",
              label: "海南"
            }
          ]
        },
        {
          value: "其他",
          label: "其他",
          children: [
            {
              value: "香港",
              label: "香港"
            },
            {
              value: "澳门",
              label: "澳门"
            },
            {
              value: "taiwan",
              label: "台湾"
            }
          ]
        }
      ],
      options2: [
        {
          value: "1",
          label: "电信"
        },
        {
          value: "2",
          label: "移动"
          // disabled: true
        },
        {
          value: "3",
          label: "联通"
        }
      ],
      value1: "",
      total_cnt: 1,
      tolpage: 0,
      pagesize: 10,
      value2: "",
      optiondisplay: false,
      rotate: false,
      tableData: []
    };
  },
  mounted() {
    this.getJson();
    this.getdatalist();
  },
  methods: {
    getJson() {
      axios.get("../../../static/cities.json").then(res => {});
    },
    handleChange(value) {
      this.getdatalist();
    },
    //请求数据
    getdatalist() {
      let parmas = new Object();
      // let ipsos = /((\d|[1-9]\d|1\d\d|2[0-4]\d|25[0-5])\.){3}(1\d\d|2[0-4]\d|25[0-5]|[1-9]\d|\d)/;
      let ipsos = /^(\d{1,3}\.{1}){3}((\d{1,3}){1})$/;
      if (ipsos.test(this.seachinput) == true) {
        parmas.nodeId = "";
        parmas.ip = this.seachinput;
      } else {
        parmas.nodeId = this.seachinput;
        parmas.ip = "";
      }
      if (this.value1[1]) {
        parmas.province = this.value1[1];
      } else {
        parmas.province = "";
      }
      if (this.value !== 0 && this.value !== 1) {
        parmas.state = -1;
      } else {
        parmas.state = this.value;
      }

      parmas.city = "";
      parmas.page = this.tolpage;
      query_node(parmas)
        .then(res => {
          if (res.status == 0) {
            if (res.data.result.length <= 0) {
              this.$message.sussess("暂无数据");
            } else {
              this.tableData = [];
              res.data.result.forEach((item, index) => {
                //上行带宽-总
                item.upbandwidth = item.totalBW.substring(
                  0,
                  item.totalBW.lastIndexOf("/")
                );
                //下行带宽-总
                item.downbandwidth = item.totalBW.substring(
                  item.totalBW.indexOf("/") + 1
                );
                //上行带宽-剩余
                item.upbandwidth_rema = item.remainingBW.substring(
                  0,
                  item.remainingBW.lastIndexOf("/")
                );
                //下行带宽-剩余
                item.downbandwidth_rema = item.remainingBW.substring(
                  item.remainingBW.indexOf("/") + 1
                );
                if (item.state == 0) {
                  item.devstatus = "在线";
                  item.bgccolor = "#5CC77D";
                } else {
                  item.devstatus = "离线";
                  item.bgccolor = "#929292";
                }
                this.tableData.push(item);
              });
            }
          } else {
            this.$message.error(res.err_msg);
          }
        })
        .catch(error => {});
    },
    //详情
    handleClick(val) {
      sessionStorage.setItem("serdata", JSON.stringify(val));
      this.$router.push({
        path: "/ipfs_location_details"
      });
    },
    //获取页码
    getpage(pages) {
      this.tolpage = pages;
      this.getdatalist();
    },
    //获取每页数量
    gettol(pagetol) {
      this.pagesize = pagetol;
      this.getdatalist();
    },
    //搜索按钮
    seachipfs() {
      this.getdatalist();
    },
    seach() {
      this.getdatalist();
    },
    //重置
    resetseach() {
      this.value = "";
      this.value1 = "";
      this.value2 = "";
      this.seachinput = "";
      this.getdatalist();
    },
    // 表头样式设置
    headClass() {
      return "text-align: center;background:#eef1f6;";
    },
    // 表格样式设置
    rowClass() {
      return "text-align: center;";
    },
    //筛选按钮
    option_display() {
      this.optiondisplay = !this.optiondisplay;
      this.rotate = !this.rotate;
    },
    //导出的方法
    exportExcel() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../excel/Export2Excel");
        const tHeader = [
          "节点ID",
          "节点状态",
          "节点IP",
          "节点城市",
          "节点分区",
          "节点运营商",
          "总带宽",
          "占用带宽",
          "剩余带宽",
          "总空间",
          "占用空间",
          "剩余空间",
          "节点平均利用率",
          "日期"
        ];
        // 上面设置Excel的表格第一行的标题
        const filterVal = [
          "node_id",
          "nodestatus",
          "node_ip",
          "nodelocation",
          "nodepartition",
          "operator",
          "allbandwidth",
          "usedbandwidth",
          "remainingbandwidth",
          "totalspace",
          "takeupspace",
          "remainingspace",
          "averageusage",
          "nowdate"
        ];
        // 上面的index、nickName、name是tableData里对象的属性
        const list = this.tableData; //把data里的tableData存到list
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "节点信息");
      });
    },

    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  },
  components: { fenye }
};
</script>

<style lang="scss" scoped>
.content {
  .seach_title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    .seach_title_input {
      width: 20%;
    }

    .resetseach_btn {
      margin-left: 15px;
    }
    .seach_top_right {
      float: left;
      height: 40px;
      line-height: 40px;
      margin-left: 10px;
    }
  }
}
.seach_bottom {
  text-align: left;
  height: 100px;
  background: #f0f5f5;
  border-radius: 10px;
  padding: 10px 15px;
  .seach_bottom_tw {
    display: flex;
    .location_select {
      margin: 0 10px;
    }
    .region_select {
      margin: 0 10px;
    }
  }
  .el-select {
    width: 150px;
  }
  .seach_bottom_btn {
    margin: 10px 10px;
  }
}
//旋转
.aa {
  transition: all 1s;
}
.go {
  transform: rotate(-180deg);
  transition: all 1s;
}
</style>



